.container{
    width: 600px;
    height: 800px;
    border-radius: 20px;
    margin: auto;
    margin-top: 75px;
    background-image: linear-gradient( cyan, blue);

}
.top-bar{
    display: flex;
    justify-content: center;
    padding-top: 60px;
    gap: 14px;
}
.top-bar input{
    width: 360px;
    height: 80px;
    display: flex;
    background: #ebfffc;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: gray;
    font-weight: 400;
    font-size: 20px;
}
.search-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 78px;
    height: 78px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;
}
.search_img{
    width: 30px;
    height: 35px;
}
.weather_img{
    display: flex;
    justify-content: center;
    margin-top: 30px;
}
.weather_temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 120px;
    font-weight: 400;
}
.weather_location{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400;
}
.data_container{
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
}
.element{
    margin: auto;
    display: flex;
    align-items: center;
    gap: 20px;
}
.data{
    font-size: 34px;
    font-weight: 400;
}
.text{
    font-size: 20px;
    font-weight: 400;
}
.icon{
    width: 40px;
    height: 40px;
}


